if (location.pathname.includes('/metod.html')) {
  $('.bulb__cta').click(function () {
    //      
    $('.bulb__cta').removeClass('bulb__cta--active');
    //     ,   
    $(this).addClass('bulb__cta--active');
  });


  $(document).ready(function () {
    // Media queries
    var mobile = window.matchMedia("(max-width: 320px)");
    var largeScreen = window.matchMedia("(min-width: 1000px)");

    $(".bulb__ctas .bulb__cta").click(function () {

      // Only proceed if the viewport width is over 1000px
      if (largeScreen.matches) {
        let bulbIndex = $(this).data("diagrams-cta");
        $(".bulb").removeClass("bulb--active");
        $(".bulb__cta").removeClass("bulb__cta--active");

        if (bulbIndex == 0) {
          $(".bulb--top").addClass("bulb--active");
        } else {
          $(".bulb--bottom").addClass("bulb--active");
        }

        $(this).addClass("bulb__cta--active");
      } else if (mobile.matches) { // When viewport width is 320px or less, disable animations
        // Turn off animations
        $(".bulb, .bulb__cta").css("transition", "none");

        // Remove the click events
        $(".bulb__ctas .bulb__cta").off("click");
      }
    });
  });
}

if (location.pathname.includes('/mission.html')) {
  document.addEventListener('DOMContentLoaded', function () {
    let flkty = new Flickity('.step-slider__slider', {
      prevNextButtons: false,
      pageDots: false,
      groupCells: 1,
      contain: false, //  contain: true
    });

    let controlsDesktop = document.querySelector('.step-slider__controls.desktop');
    let controlsMobile = document.querySelector('.step-slider__controls.mobile');

    let prevButtonDesktop = controlsDesktop.querySelector('.js-arrow-left');
    let nextButtonDesktop = controlsDesktop.querySelector('.js-arrow-right');

    let prevButtonMobile = controlsMobile.querySelector('.round-button--left');
    let nextButtonMobile = controlsMobile.querySelector('.round-button--right');

    [prevButtonDesktop, prevButtonMobile].forEach(button => {
      button.addEventListener('click', function () {
        flkty.previous();
      });
    });

    [nextButtonDesktop, nextButtonMobile].forEach(button => {
      button.addEventListener('click', function () {
        flkty.next();
      });
    });

    function updateButtons() {
      if (flkty.selectedIndex === 0) {
        [prevButtonDesktop, prevButtonMobile].forEach(button => {
          button.classList.add('disabled');
        });
      } else {
        [prevButtonDesktop, prevButtonMobile].forEach(button => {
          button.classList.remove('disabled');
        });
      }

      if (flkty.selectedIndex === flkty.slides.length - 1) {
        [nextButtonDesktop, nextButtonMobile].forEach(button => {
          button.classList.add('disabled');
        });
      } else {
        [nextButtonDesktop, nextButtonMobile].forEach(button => {
          button.classList.remove('disabled');
        });
      }
    }

    flkty.on('change', updateButtons);
    flkty.on('settle', updateButtons);

    flkty.on('change', function (index) {
      let numbers = document.querySelectorAll('.step-slider__number');
      numbers.forEach(function (number) {
        number.classList.remove('active');
        number.classList.add('inactive');
      });

      let activeSlide = flkty.cells[index].element;
      let activeNumber = activeSlide.querySelector('.step-slider__number');
      activeNumber.classList.add('active');
      activeNumber.classList.remove('inactive');
    });
  });

}

const sr = ScrollReveal();

function revealAnimation(element, options) {
  sr.reveal(element, {
    origin: options.origin || 'right',
    distance: options.distance || '50px',
    duration: options.duration || 1000,
    delay: options.delay || 200,
    reset: options.reset || false,
  });
}

// Corrected: replaced 'reveal' with 'revealAnimation'
