if (location.pathname.includes('/contact.html')) {
  document.addEventListener('DOMContentLoaded', function () {
    const headerLangSelect = document.getElementById('js-header-lang');
    const burgerLangSelect = document.getElementById('js-burger-lang');
    const footerLangSelect = document.getElementById('js-footer-lang');

    const elements = [
      {
        selector: '#locSection', //  
        titleClass: '.title-text-sample__title', //  
        enText: ["The world's best experts within a hand's reach. Which of your challenges could this solve?"], //   
        ruText: ["     .       ?"], //   
      },
    ];

    function updateText(selectedLang) {
      elements.forEach((element) => {
        const target = document.querySelector(element.selector);

        if (target) {
          const title = target.querySelector(element.titleClass);

          if (element.selector === '#locSection') {
            //     "Using technology to grow a business",       
            if (selectedLang === 'ru') {
              title.textContent = element.ruText[0];
            } else {
              title.textContent = element.enText[0];
            }
          }
        }
      });
    }

    //        
    updateText(headerLangSelect.value);

    //       
    headerLangSelect.addEventListener('change', function () {
      const selectedLang = headerLangSelect.value;
      updateText(selectedLang);
    });

    //      -
    burgerLangSelect.addEventListener('change', function () {
      const selectedLang = burgerLangSelect.value;
      updateText(selectedLang);
    });

    //      
    footerLangSelect.addEventListener('change', function () {
      const selectedLang = footerLangSelect.value;
      updateText(selectedLang);
    });
  });
}



